.todoList{width:100%;padding:50px 0 100px;color:#333333;height:100%;overflow:auto}.todoList-title{text-align:center;font-weight:bold;color:#666666}.todoList-title h1{color:#333333;line-height:50px}.todoList-edit{position:relative;border:1px solid #cccccc;border-radius:6px;width:80%;max-width:600px;margin:30px auto}.todoList-edit .add{display:inline-block;font-size:18px;width:40px;height:40px;text-align:center;line-height:40px;background-color:#efefef;border-radius:0 5px 5px 0}.todoList-edit .add:hover{color:#1890ff;cursor:pointer}.todoList-edit input{border-radius:5px 0 0 5px;height:40px;line-height:40px;font-size:16px;width:calc(100% - 40px);outline:none;padding:0 6px}.todoList-item{width:80%;max-width:600px;position:relative;margin:0 auto;min-height:40px;line-height:40px;border-bottom:1px solid #efefef}.todoList-item:hover{background-color:#efefef}.todoList-item .status,.todoList-item .delete{font-size:14px;display:inline-block;width:60px;text-align:center;color:#1890ff;cursor:pointer;vertical-align:top}.todoList-item .delete{color:#ccc;width:40px}.todoList-item .delete:hover{color:#1890ff}.todoList-item .content{display:inline-block;width:calc(100% - 100px);font-size:15px;text-align:justify;line-height:40px;word-break:break-all}.todoList-item.done .status{color:#cccccc}.todoList-item.done .content{color:#cccccc;text-decoration:line-through}
